import React from 'react'

import styled from 'styled-components'

import { Swiper, SwiperSlide } from 'swiper/react';

import { ThemeProvider } from '@material-ui/core/styles';

import * as Material from '@material-ui/core';

import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import * as Styles from '../styles'

import * as UI from '../components'

const ImageSlider = styled(Swiper)`
  .swiper-scrollbar {
    background: gray;
  }
  .swiper-scrollbar-drag {
    background: white;
  }
  .swiper-pagination-bullet {
    background: gray;
    opacity: 1;
  }
  .swiper-pagination-bullet-active {
    background: white;
  }
`

const Placeholder = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  ${Styles.text.primary};
  font-size: 2rem;
  text-align: center;
  word-break: break-word;
  
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border: dashed 0.25rem gray;
  border-radius: 0.5rem;
`


const weightRegex = /[R$\s]*([0-9.,]*)/
function sumDetails({ list, name, regex }) {
  return list
    .map(item => {
      let weight = item.details.filter(detail => detail.name === name)
      if (weight.length) return Number.parseFloat(weight[0].value.match(regex)[1]) || 0
      return 0
    })
    .reduce((acc, act) => acc + act, 0)
}

export default function Orçamento(props = {}) {

  const {
    dimensions,
    refs,
    removeItem,
    notifications,
    cart = [],
  } = props

  const ref = React.useRef()
  const mainRef = React.useRef()

  const [details, setDetails] = React.useState([
    { name: 'Total de Cestas', value: cart.length },
    { name: 'Peso', value: '0 KG' },
    { name: 'Contém', value: '0 itens' },
    { name: 'Preço', value: 'R$ 0,00', highlight: true },
  ])

  React.useEffect(() => {
    const totalWeight = sumDetails({
      list: cart,
      name: 'Peso',
      regex: weightRegex,
    })
    const totalItems = sumDetails({
      list: cart,
      name: 'Contém',
      regex: weightRegex,
    })
    const totalPrice = sumDetails({
      list: cart,
      name: 'Preço',
      regex: weightRegex,
    })
    setDetails([
      { name: 'Total de Cestas', value: cart.length },
      { name: 'Peso', value: `${totalWeight} KG` },
      { name: 'Contém', value: `${totalItems} itens` },
      { name: 'Preço', value: `R$ ${totalPrice.toFixed(2)}`, highlight: true },
    ])

  }, [cart])

  const update = React.useCallback(function update() {
    setTimeout(() => {
      ref.current.swiper.update()
      mainRef.current.swiper.update()
      mainRef.current.swiper.scrollbar.updateSize()
    }, 100)
  }, [ref, mainRef])

  React.useEffect(update, [cart, dimensions, update, ref, mainRef])

  function onClickAddProduct() {
    (refs?.site?.current?.swiper).slidePrev()
  }

  function paymentMethods() {
    if (cart.length) {
      if (props.onChangeForm) props.onChangeForm({ Carrinho: cart });
      (refs?.section?.current?.swiper).slideNext()
    } else {
      notifications.notify({ message: 'Adicione alguns itens no carrinho!', warning: true })
    }
  }

  function onClickRemoveItem({ index }) {
    return () => {
      removeItem(index)
      update()
    }
  }

  return (
    <ThemeProvider theme={Styles.themes.dark}>
      <div style={{ ...Styles.center, ...Styles.full }}>
        <div style={{ ...Styles.full, ...Styles.overlay, display: 'flex' }}>
          <div style={Styles.full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={1}
              style={Styles.full}
              freeMode
              nested
              ref={ref}
            >
              <SwiperSlide
                style={{
                  height: 'auto'
                }}
              >
                <Material.Container>
                  <div
                    style={{
                      padding: '2rem',
                    }}
                  >
                    <div style={{
                      padding: '2rem',
                      position: 'relative'
                    }}>
                      <div style={{
                        ...Styles.background.primary,
                        ...Styles.border.radius,
                        ...Styles.fullAbsolute,
                        //...box.shadow,
                        //opacity: 0.85
                      }} />
                      <Material.Typography variant="h4" style={{ color: 'white' }}>
                        Orçamento
                      </Material.Typography>

                      <Material.Breadcrumbs
                        separator={<NavigateNextIcon fontSize="small" />}
                        aria-label="breadcrumb"
                      >
                        <Material.Link color="textPrimary" >
                          Orçamento
                        </Material.Link>
                        <Material.Link color="inherit" >
                          Formas de Pagamento
                        </Material.Link>
                        <Material.Link color="inherit" >
                          Detalhes do Frete
                        </Material.Link>
                        <Material.Link color="inherit" >
                          Agendamento
                        </Material.Link>
                      </Material.Breadcrumbs>
                      <div style={{ paddingBottom: '3rem' }} />
                      {
                        details.map((detail, index) => {
                          return (
                            <UI.Box.Row key={`detail[${detail.name}][${index}]`} style={{ justifyContent: 'space-between', flexWrap: 'wrap' }}>
                              <Material.Typography
                                variant={detail.highlight ? "h4" : "h5"}
                                color="secondary"
                                style={Styles.foreground.secondary}
                              >
                                {detail.name}
                              </Material.Typography>
                              <Material.Typography
                                variant={detail.highlight ? "h3" : "h4"}
                                style={Styles.foreground.light}
                              >
                                {detail.value}
                              </Material.Typography>
                            </UI.Box.Row>
                          )
                        })
                      }
                      <div style={{ paddingBottom: '3rem' }} />
                      <UI.Builder.Slides
                        SwiperComponent={ImageSlider}
                        useRef={mainRef}
                        //slides={item.images.length ? item.images : [{}]}
                        //slides={item.images ? [...item.images] : [{}]}
                        slides={cart.length ? cart.map(item => item.images[0]) : [{}]}
                        style={{
                          zIndex: 1,
                          //height: '15rem',
                          paddingBottom: '1rem',
                        }}
                        speed={500}
                        autoplay={false}
                        scrollbar={{ draggable: true/* , hide: true */ }}
                        imageStyle={{
                          objectFit: 'contain',
                          width: '80%',
                          height: '80%',
                          margin: '10%',
                        }}
                        slideProps={{
                          style: {
                            width: '15rem',
                            height: '15rem'
                          }
                        }}
                        slidesPerView='auto'
                        //slidesPerView={2.5}
                        //slidesPerColumn={2}
                        freeMode
                        effect="slide"
                        direction="horizontal"
                        renderItem={({ content }, index) => {
                          if (!index && !cart.length) {
                            return (
                              <div
                                style={{
                                  ...Styles.full,
                                  ...Styles.center,
                                  cursor: 'pointer',
                                }}
                                onClick={onClickAddProduct}
                              >
                                <div
                                  style={{
                                    ...Styles.center,
                                    zIndex: -1,
                                    borderRadius: '1vmin',
                                    backgroundColor: Styles.palette.light,
                                    width: '90%',
                                    height: '90%',
                                    boxSizing: 'border-box',
                                    padding: '1rem',
                                  }}
                                >
                                  <Placeholder>
                                    Adicione sua cesta
                                  </Placeholder>
                                </div>
                              </div>
                            )
                          }
                          return (
                            <div style={{
                              ...Styles.full,
                            }}>
                              <div
                                style={{
                                  ...Styles.fullAbsolute,
                                  zIndex: -1,
                                  borderRadius: '1vmin',
                                  width: '90%',
                                  height: '90%',
                                  margin: '5%',
                                  backgroundColor: Styles.palette.light,
                                  //...box.shadow
                                  //opacity: '0.8'
                                }}
                              />
                              <div
                                style={{
                                  position: 'absolute',
                                  //bottom: 0,
                                  //right: 0,
                                  padding: '6%',
                                  color: Styles.palette.alert
                                }}
                              >
                                <div
                                  onClick={onClickRemoveItem({ index })}
                                  style={{
                                    ...Styles.text.primary,
                                    padding: '0.25rem',
                                    color: Styles.palette.light,
                                    //border: 'solid 1px red',
                                    borderRadius: '0.5rem',
                                    backgroundColor: 'rgba(255,0,0,0.75)',
                                    cursor: 'pointer'
                                  }}
                                >Remover item</div>
                              </div>
                              {content}
                            </div>
                          )
                        }}
                      />
                      <div style={{ paddingBottom: '1rem' }} />
                      <Material.Button
                        variant="contained"
                        color="secondary"
                        style={{ width: '100%' }}
                        onClick={paymentMethods}
                      >
                        <Material.Typography variant="h4">
                          COMPRAR AGORA
                        </Material.Typography>
                      </Material.Button>
                    </div>
                  </div>
                </Material.Container>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}