import React from 'react'

const Row = ({ style, ...props }) => (
  <div
    style={{ flexDirection: 'row', flex: 1, display: 'flex', ...style }}
    {...props}
  />
)

const Column = ({ style, ...props }) => (
  <div
    style={{ flexDirection: 'column', flex: 1, display: 'flex', ...style }}
    {...props}
  />
)

const View = (props) => (
  <div {...props} />
)

export {
  Row,
  Column,
  View,
}

export default {
  Row,
  Column,
  View,
}