import React from 'react'

import styled from 'styled-components'

import { Swiper, SwiperSlide } from 'swiper/react';

import { ThemeProvider } from '@material-ui/core/styles';

import ReactMarkdown from 'react-markdown'

import {
  full,
  center,
  palette,
  background,
  fullAbsolute,
  themes,
} from '../styles'

import * as UI from '../components'

import {
  //Typography,
  Container,
} from '@material-ui/core';

const Content = styled(ReactMarkdown)`
  color: ${palette.primary};
  font-size: 125%;
  font-family: Saira;
  text-align: justify;

  img {
    padding: 2.5%;
    width: 50%;
    border-radius: 2rem;
  }

  img:nth-child(odd) {
    float: left;
  }

  img:nth-child(even) {
    float: right;
  }
`

export default function Sobre(props = {}) {

  const {
    dimensions,
    siteData,
  } = props

  const ref = React.useRef()

  const [content, setContent] = React.useState('')

  React.useEffect(() => {
    const rawData = siteData['Sobre']
    if (rawData) setContent(rawData.map(Object.values).join('\n'))
  }, [siteData])

  const update = React.useCallback(function update() {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  const Image = React.useMemo(() => function Image(props) {
    return <img onLoad={update} {...props} alt="about content" />
  }, [update])

  React.useEffect(() => {
    setTimeout(update, 200)
  }, [content, ref, update, dimensions])

  const theme = React.useMemo(() => themes.light, [])

  return (
    <ThemeProvider theme={theme}>
      <div style={{ ...center, ...full }}>
        <div style={{ ...fullAbsolute, ...background.light, opacity: 0.9 }} />
        <div style={{ ...full,/*  ...overlay,  */display: 'flex' }}>
          <div style={full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={0}
              style={full}
              freeMode
              nested
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <div style={{ display: 'flex' }}>
                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                    }}
                  >
                    <UI.LoadingComponent loading={props.loadingData} />
                    <UI.Box.Column>
                      <Container>
                        <Content renderers={{ image: Image }} {...{ theme }}>
                          {content}
                        </Content>
                      </Container>
                    </UI.Box.Column>
                  </UI.Box.Column>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}