import React from 'react'

import * as Router from "react-router-dom";

import styled from 'styled-components'

import axios from 'axios'

import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import SwiperCore, * as SwiperModules from 'swiper';

//import 'swiper/swiper.scss';
//import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/zoom/zoom.scss';
//import 'swiper/components/scrollbar/scrollbar.scss';

import './index.css';
import './swiper.scss';
import './navigation.scss';
import './scrollbar.scss';

import * as Scenes from './scenes'

import * as UI from './components'

import * as Hooks from './hooks'

SwiperCore.use([
  SwiperModules.Navigation,
  SwiperModules.Pagination,
  SwiperModules.Scrollbar,
  SwiperModules.A11y,
  SwiperModules.Autoplay,
  SwiperModules.EffectFade,
  SwiperModules.Zoom,
  //SwiperModules.HashNavigation,
  //SwiperModules.History,
]);

const NotificationsContainer = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
`

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function App() {

  const { ref, dimensions } = Hooks.useDimensions()

  const refSite = React.useRef()

  const [cart, setCart] = React.useState([])
  const [encartes, setEncartes] = React.useState([])
  const [siteData, setSiteData] = React.useState({})
  const [formValues, setFormValues] = React.useState({})
  const [loadingData, setLoadingData] = React.useState(true)

  const [open, setOpen] = React.useState(false);
  const [alertProps, setAlertProps] = React.useState({})

  const api = React.useMemo(() => axios.create({
    //baseURL: 'http://localhost:5000',
    baseURL: 'https://mrcestasapp.herokuapp.com',
    //withCredentials: true,
  }), [])

  const notifications = React.useMemo(() => ({
    notify: function ({ message, success, error }) {
      let severity = "warning"
      if (success) severity = "success"
      if (error) severity = "error"
      setAlertProps({
        children: message,
        severity,
      })
      setOpen(true)
    }
  }), [])

  React.useEffect(() => {
    async function fetchData() {
      try {
        api.get('/populate-data/1f292dcf.668556')
          .then(response => {
            if (response.status === 200) {
              const { data } = response

              setSiteData(data)
              setLoadingData(false)

              if (data['Encartes']) {
                setEncartes(data['Encartes'].map(item => {
                  const { Nome, Imagens, ...rest } = item
                  const name = Nome
                  const images = Imagens.split('\n').filter(part => !!part)
                  return {
                    name,
                    images,
                    details: Object.keys(rest).map(detail => ({
                      name: detail,
                      value: rest[detail],
                      ...(
                        detail === 'Preço'
                          ? { highlight: true }
                          : {}
                      )
                    }))
                  }
                }))
              }
            }
          })
          .catch(response => {
          })
      } catch (error) {
      }
    }
    fetchData()
  }, [api])

  function addItem(item) {
    setCart(cart => cart.concat(item))
  }

  const removeItem = React.useCallback(function removeItem(index) {
    setCart(cart => {
      const newList = [...cart]
      newList.splice(index, 1)
      return newList
    })
  }, [])

  function handleClose() {
    setOpen(false)
  }

  function getRefSite(ref) {
    refSite.current = ref.current
  }

  function onChangeForm(values) {
    setFormValues(actualValues => {
      const result = Object.assign(actualValues, values)
      return result
    })
  }

  function cleanForm() {
    setCart([])
    setFormValues({})
  }

  const providerData = React.useMemo(() => ({
    dimensions,
    siteData,
    api,
    notifications,
    loadingData,
  }), [
    dimensions,
    siteData,
    api,
    notifications,
    loadingData,
  ])

  const path = React.useMemo(() => [
    `/bem-vindo`,
    `/encartes`,
    //`/encartes/:name?`,
    `/carrinho`,
    `/história`,
    `/contato`,
  ], [])

  const structure = React.useMemo(() => [
    [<Scenes.BoasVindas {...{ ...providerData }} />],
    {
      props: {
        navigation: true,
      },
      content:
        encartes.map(item => (
          <Scenes.Encarte
            {...{ item, addItem }}
            {...{ ...providerData }}
          />
        )),
    },
    {
      props: {
        scrollbar: false,
        allowTouchMove: false,
      },
      content: [
        <Scenes.Orçamento
          {...{ cart, removeItem, onChangeForm }}
          {...{ ...providerData }}
        />,
        <Scenes.Pagamento
          {...{ cart, onChangeForm }}
          {...{ ...providerData }}
        />,
        <Scenes.Frete
          {...{ onChangeForm }}
          {...{ ...providerData }}
        />,
        <Scenes.Agendamento
          {...{ onChangeForm, formValues, cleanForm }}
          {...{ ...providerData }}
        />,
      ]
    },
    [
      <Scenes.Sobre {...{ ...providerData }} />,
      <Scenes.História {...{ ...providerData }} />,
    ],
    [<Scenes.Contato {...providerData} />]
  ], [encartes, cart, providerData, formValues, removeItem])

  return (
    <div style={{ width: '100%', height: '100%' }} ref={ref}>
      <Router.BrowserRouter>
        <Router.Switch>
          <Router.Route path={path} exact>
            <>
              <UI.Builder.Site
                getRef={getRefSite}
                path={path}
                structure={structure}
              />
            </>
          </Router.Route>
          <Router.Redirect to="/bem-vindo" />
        </Router.Switch>
      </Router.BrowserRouter>
      <NotificationsContainer>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert onClose={handleClose} {...alertProps} />
        </Snackbar>
      </NotificationsContainer>
    </div>
  )
};