import React from 'react'

import yaml from 'js-yaml'

import {
  FormControl,
  InputLabel,
  FilledInput,
  Select,
  MenuItem,
} from '@material-ui/core';

function RenderField(props = {}) {

  const { field, onChange, name } = props

  const [open, setOpen] = React.useState(false)

  const [value, setValue] = React.useState('')

  const toggleSelect = React.useCallback(() => {
    setOpen(!open)
  }, [open, setOpen])

  function changeSelect(event) {
    setValue(event.target.value)
    if (onChange) onChange(field.name, event.target.value)
  }

  function onChangeInput(event) {
    setValue(event.target.value)
    if (onChange) onChange(field.name, event.target.value)
  }

  return (
    <div>
      <FormControl error={field.required && !value} variant="filled" color="primary" style={{ width: '100%' }}>
        <InputLabel required={field.required} id={`label[${name}][${field.name}]`}>
          {field.label}
        </InputLabel>
        {
          !field.select && (
            <FilledInput
              //labelId={`label${field.name}`}
              id={`label[${name}][${field.name}]`}
              onChange={onChangeInput}
              value={value}
            />
          )
        }
        {
          field.select && (
            <Select
              //labelId={`label${field.name}`}
              id={`select[${name}][${field.name}]`}
              value={value}
              open={open}
              onClick={toggleSelect}
              onChange={changeSelect}
            >
              <MenuItem value="">Escolha uma opção</MenuItem>
              {
                field.select.map(option => (
                  <MenuItem
                    key={`option${field.name}${option.value}`}
                    value={option.value}
                  >
                    {option.label}
                  </MenuItem>
                ))
              }
            </Select>
          )
        }
      </FormControl>
      <div style={{ paddingBottom: '2%' }} />
    </div>
  )
}

function Formulary(props = {}) {

  const { /* form = [] */ data, name } = props

  const [form, setForm] = React.useState([])

  function initialValues(form) {
    return Object.assign({}, ...form.map(field => ({ [field.name]: '' })))
  }

  const [values, setValues] = React.useState(initialValues(form))

  React.useEffect(() => {
    setValues(initialValues(form))
  }, [form])

  React.useEffect(() => {
    const object = Object.assign({}, ...Object.keys(data).map(key => ({ [key]: yaml.safeLoad(data[key]) })))

    const formData = Object.keys(object).map(key => {
      const field = object[key]
      switch (field.tipo) {
        case 'texto':
          return {
            label: key,
            name: key, //slugify(key),
            required: field['obrigatório'] === 'sim'
          }
        case 'lista':
          return {
            label: key,
            name: key,
            select: field['opções'].map(item => ({
              label: item,
              value: item,
            })),
            required: field['obrigatório'] === 'sim'
          }
        default:
          return undefined
      }
    }).filter(field => !!field)

    setForm(formData)
  }, [data])

  function onChange(name, value) {
    setValues(values => ({
      ...values,
      [name]: value
    }))
  }

  const Component = (
    <>
      {form.map((item, index) => (
        <RenderField
          name={name}
          key={`field[${index}]`}
          onChange={onChange}
          field={item}
        />
      ))}
    </>
  )
  return {
    Component,
    values,
    form,
  }
}

export default Formulary