import React, { useRef, useEffect } from 'react'

import * as router from "react-router-dom";

import styled from 'styled-components'

import { Swiper, SwiperSlide } from 'swiper/react';

import { ThemeProvider } from '@material-ui/core/styles';

import {
  full,
  center,
  background,
  fullAbsolute,
  themes,
  vertical,
  palette,
} from '../styles'

import { Box } from '../components'

import { logoWhite } from '../assets'

import {
  Button,
  Typography,
  Container,
  CircularProgress,
} from '@material-ui/core';

const SwiperComponent = styled(Swiper)`
  .swiper-scrollbar-drag {
    //background: ${palette.secondary};
  }
`

const LoadingContainer = styled.div`
  //width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 2rem;
`

const LoadingComponent = styled(CircularProgress)`
  //width: 6rem;
  //height: 6rem;
`

/* 

  const data = [
    {
      image: products.product1,
      label: 'Anúncios',
    },
*/

export default function BoasVindas(props = {}) {

  const [data, setData] = React.useState([])

  const history = router.useHistory()

  const {
    dimensions,
    //item,
    refs,
    siteData,
    //sectionIndex,
  } = props

  const ref = useRef()

  useEffect(() => {
    ref.current.swiper.update()
  }, [dimensions])

  const update = React.useCallback(function update() {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  React.useEffect(() => {
    const rawData = siteData['BoasVindas'] || []
    setData(rawData.map(item => ({
      label: item['Categoria'],
      link: item['Link'],
      image: item['Imagem'],
    })))
    setTimeout(update, 200)
  }, [siteData, update])

  function slideNext() {
    (refs?.site?.current?.swiper).slideNext();
  }

  function onClickNavigate(link) {
    return () => {
      if (link) {
        if (link.startsWith('/')) history.push(link)
        else {
          //window.location.href = link
          let tab = window.open(link, '_blank');
          tab.focus()
        }
      }
    }
  }

  return (
    <ThemeProvider theme={themes.light}>
      <div style={{ ...center, ...full }}>
        <div style={{ ...fullAbsolute, ...background.light, opacity: 0.9 }} />
        <div style={{ ...full,/*  ...overlay,  */display: 'flex' }}>
          <div style={full}>
            <SwiperComponent
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={0}
              style={full}
              freeMode
              nested
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <Box.View style={{ position: 'relative' }}>
                  <div
                    style={{
                      ...full,
                      ...center,
                      ...vertical,
                    }}
                  >
                    <div
                      style={{
                        ...fullAbsolute,
                        //...overlay,
                        ...background.primary,
                        //opacity: 0.85,
                        //margin: '5vmin',
                        //borderRadius: '1em'
                        transform: 'skewY(3.5deg)',
                        height: '110%',
                        top: '-10%',
                      }}
                    />
                    <Box.View style={{ padding: '2rem' }}>
                      <img
                        alt="logo"
                        style={{
                          width: '100%',
                          height: '100%'
                        }}
                        src={logoWhite}
                      />
                    </Box.View>
                    <Box.View style={{ padding: '2rem' }}>
                      <Typography variant="h4" style={{ color: 'white' }}>
                        Seja bem vindo!
                      </Typography>
                    </Box.View>
                  </div>
                </Box.View>
                <div style={{ display: 'flex' }}>
                  <Box.Column
                    style={{
                      padding: '5vmin',
                    }}
                  >
                    <Box.Column>
                      <Container>
                        {
                          data.map((item, index) => (
                            <Box.View key={`heroSection${index}`}>
                              <Typography variant="h4" color="primary">
                                {item.label}
                              </Typography>
                              <Box.View
                                style={{
                                  //padding: '2.5%',
                                  display: 'flex',
                                  overflow: 'hidden',
                                  margin: '2.5%',
                                  backgroundColor: 'white',
                                  borderRadius: '1em',
                                  boxShadow: '0 0 1em #ccc',
                                  ...(
                                    item.link
                                      ? { cursor: 'pointer' }
                                      : {}
                                  )
                                }}
                                onClick={onClickNavigate(item.link)}
                              >
                                <img
                                  src={item.image}
                                  alt="img"
                                  onLoad={update}
                                  style={{
                                    width: '100%',
                                    height: '100%'
                                  }}
                                />
                              </Box.View>
                              <div style={{ paddingBottom: '5%' }} />
                            </Box.View>
                          ))
                        }
                        {
                          !props.loadingData ? null : (
                            <LoadingContainer>
                              <LoadingComponent size='6rem' />
                            </LoadingContainer>
                          )
                        }
                        <Button
                          variant="contained"
                          color="secondary"
                          style={{ width: '100%' }}
                          onClick={slideNext}
                        >
                          <Typography variant="h4">
                            Ver produtos
                          </Typography>
                        </Button>
                      </Container>
                    </Box.Column>
                  </Box.Column>
                </div>
              </SwiperSlide>
            </SwiperComponent>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}