import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { ThemeProvider } from '@material-ui/core/styles';

import * as Material from '@material-ui/core';

import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import * as Styles from '../styles'

import * as UI from '../components'

const weightRegex = /[R$\s]*([0-9.,]*)/
function sumDetails({ list, name, regex }) {
  return list
    .map(item => {
      let weight = item.details.filter(detail => detail.name === name)
      if (weight.length) return Number.parseFloat(weight[0].value.match(regex)[1]) || 0
      return 0
    })
    .reduce((acc, act) => acc + act, 0)
}

export default function Pagamento(props = {}) {

  const {
    dimensions,
    cart = [],
    refs,
    siteData,
    notifications,
  } = props

  const ref = React.useRef()

  const [data, setData] = React.useState({})

  const {
    Component: FormularyComponent,
    values,
    form,
  } = UI.Formulary({
    data
  })

  React.useEffect(() => {
    const rawData = siteData['*Pagamento']
    setData(rawData && rawData.length ? rawData[0] : {})
  }, [siteData])

  const [details, setDetails] = React.useState(() => {
    const totalPrice = sumDetails({
      list: cart,
      name: 'Preço',
      regex: weightRegex,
    })
    return [
      {
        name: 'total',
        label: 'Total',
        value: `R$ ${totalPrice.toFixed(2)}`,
        highlight: true
      },
    ]
  })

  React.useEffect(() => {
    const totalPrice = sumDetails({
      list: cart,
      name: 'Preço',
      regex: weightRegex,
    })
    let parcels = []
    if (values['Quantidade de Parcelas']) {
      const parcelsValueRaw = values['Quantidade de Parcelas'].match(weightRegex)[1]
      const parcelsValue = parcelsValueRaw ? Number.parseInt(parcelsValueRaw) : 0
      if (parcelsValue && totalPrice) parcels = [
        {
          name: 'quantidade',
          label: 'Quantidade de Parcelas',
          value: values['Quantidade de Parcelas']
        },
        {
          name: 'valor',
          label: 'Valor da Parcela',
          value: `R$ ${(totalPrice / parcelsValue).toFixed(2)}`
        }
      ]
    }
    setDetails([
      {
        name: 'total',
        label: 'Total',
        value: `R$ ${totalPrice.toFixed(2)}`,
        highlight: true
      },
      ...parcels
    ])
  }, [cart, form, values])

  const update = React.useCallback(() => {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  React.useEffect(() => {
    setTimeout(update, 200)
  }, [form, update, dimensions])

  React.useEffect(() => {
    const { swiper } = ref.current
    const { scrollbar } = swiper
    const { dragEl } = scrollbar
    dragEl.style.background = 'white'
  }, [ref])

  function validateForm() {
    const results = form.map(field => {
      if (field.required && !values[field.name]) return false
      return true
    })
    return results.filter(result => !!result).length === results.length
  }

  function nextSectionSlide() {
    if (validateForm()) {
      if (props.onChangeForm) props.onChangeForm({ Pagamento: values });
      (refs?.section?.current?.swiper).slideNext()
    } else {
      notifications.notify({ message: 'Preencha o formulário corretamente', error: true })
    }
  }

  return (
    <ThemeProvider theme={Styles.themes.dark}>
      <div style={{ ...Styles.center, ...Styles.full }}>
        <div style={{ ...Styles.fullAbsolute, ...Styles.background.primary, opacity: 0.9 }} />
        <div style={{ ...Styles.full,/*  ...overlay,  */display: 'flex' }}>
          <div style={Styles.full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={1}
              style={Styles.full}
              freeMode
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <div style={{ display: 'flex' }}>
                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                      boxSizing: 'border-box',
                      width: '100%',
                    }}
                  >
                    <UI.Box.Column
                    >
                      <Material.Container>
                        <Material.Typography variant="h4" style={{ color: 'white' }}>
                          Formas de Pagamento
                        </Material.Typography>

                        <Material.Breadcrumbs
                          separator={<NavigateNextIcon fontSize="small" />}
                          aria-label="breadcrumb"
                        >
                          <Material.Link color="inherit" >
                            Orçamento
                          </Material.Link>
                          <Material.Link color="textPrimary" >
                            Formas de Pagamento
                          </Material.Link>
                          <Material.Link color="inherit" >
                            Detalhes do Frete
                          </Material.Link>
                          <Material.Link color="inherit" >
                            Agendamento
                          </Material.Link>
                        </Material.Breadcrumbs>
                        <div style={{ paddingBottom: '5vmin' }} />
                        <Material.Container>
                          <Material.Typography variant="h6" style={{ color: 'white' }}>
                            Estamos quase lá! Agora você precisa escolher o método de pagamento desejado e também pode simular os valores das parcelas
                          </Material.Typography>
                          <div style={{ paddingBottom: '2%' }} />
                          <UI.LoadingComponent
                            loading={props.loadingData}
                            styleProgress={{
                              color: 'white'
                            }}
                          />
                          {FormularyComponent}
                          <div style={{ paddingBottom: '2%' }} />
                          <Material.Paper style={{ padding: '1rem' }} variant="elevation" elevation={10}>
                            <Material.Typography variant="h5">
                              Detalhes do pagamento
                            </Material.Typography>
                            <div style={{ paddingBottom: '2%' }} />
                            {
                              details.map(detail => (
                                <UI.Box.Row key={`detail${detail.name}`} style={{ justifyContent: 'space-between', flexWrap: 'wrap' }}>
                                  <Material.Typography
                                    variant={detail.highlight ? "h4" : "h5"}
                                    color="secondary"
                                  >
                                    {detail.label}
                                  </Material.Typography>
                                  <Material.Typography
                                    variant={detail.highlight ? "h3" : "h4"}
                                  >
                                    {detail.value}
                                  </Material.Typography>
                                </UI.Box.Row>
                              ))
                            }
                            <div style={{ paddingBottom: '2%' }} />
                            <Material.Button
                              variant="contained"
                              color="secondary"
                              style={{ width: '100%' }}
                              onClick={nextSectionSlide}
                            >
                              <Material.Typography variant="h4">
                                Finalizar pagamento
                              </Material.Typography>
                            </Material.Button>
                          </Material.Paper>
                        </Material.Container>
                      </Material.Container>
                    </UI.Box.Column>
                  </UI.Box.Column>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}