import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { BuildScene } from '../components'

import * as Styles from '../styles'

export default function BuildSection(props = {}) {

  const { scenes, refs, index: sectionIndex, ...rest } = props

  const ref = React.useRef()

  React.useEffect(() => {
    setTimeout(() => {
      ref.current.swiper.update()
      ref.current.swiper.scrollbar.updateSize()
      if (rest.allowTouchMove !== false) ref.current.swiper.allowTouchMove = scenes.length > 1
    }, 1000)
  }, [scenes, rest.allowTouchMove])

  return (
    <Swiper
      ref={ref}
      spaceBetween={0}
      slidesPerView={1}
      threshold={15}
      //nested="true"
      grabCursor
      //navigation
      /* history={{
        key: 'a',
        replaceState: true,
      }} */
      direction="horizontal"
      style={Styles.full}
      allowTouchMove={scenes.length > 1}
      //pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}
      {...rest}
    >
      {
        scenes.map((scene, index) => (
          <SwiperSlide
            key={`scene${index}`}
          /* {...({
            'data-history': `section${index}`
          })} */
          >
            <BuildScene
              sectionIndex={sectionIndex}
              scene={scene}
              refs={{
                ...refs,
                //[`section${sectionIndex}`]: ref
                section: ref
              }} />
          </SwiperSlide>
        ))
      }
    </Swiper>
  )
}