import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import * as Material from '@material-ui/core';

import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import { ThemeProvider } from '@material-ui/core/styles';

import * as Styles from '../styles'

import * as UI from '../components'

export default function Agendamento(props = {}) {

  const {
    dimensions,
    refs,
    notifications,
    siteData,
    api,
  } = props

  const ref = React.useRef()

  const [data, setData] = React.useState({})

  const [loadingSubmit, setLoadingSubmit] = React.useState(false)

  React.useEffect(() => {
    const rawData = siteData['*Agendamento']
    setData(rawData && rawData.length ? rawData[0] : {})
  }, [siteData])

  const {
    Component: FormularyComponent,
    values,
    form,
  } = UI.Formulary({
    data,
    name: 'Agendamento'
  })

  const update = React.useCallback(() => {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  React.useEffect(() => {
    setTimeout(update, 200)
  }, [form, update, dimensions])

  function validateForm() {
    const results = form.map(field => {
      if (field.required && !values[field.name]) return false
      return true
    })
    return results.filter(result => !!result).length === results.length
  }

  function nextSectionSlide() {
    setLoadingSubmit(true)
    if (validateForm()) {
      if (props.onChangeForm) {
        props.onChangeForm({ Agendamento: values });

        const params = Object.assign({},
          props.formValues.Agendamento,
          props.formValues.Frete,
          props.formValues.Pagamento,
          {
            Produtos: props.formValues.Carrinho.map(item => {
              return [
                `Nome: ${item.name}`,
                ...item.details
                  //.filter(({ name }) => ['Preço', 'Peso', 'Contém'].indexOf(name) !== -1)
                  .map(detail => `${detail.name}: ${detail.value}`)
              ].join('\n')
            }).join('\n\n')
          }
        )

        api.get('/make-order/1f292dcf.668556', { params })
          .then(response => {
            notifications.notify({ message: 'Pedido realizado com sucesso, entraremos em contato em breve!', success: true })
            setLoadingSubmit(false);
            (refs?.section?.current?.swiper).slideTo(0)
            if (props.cleanForm) props.cleanForm()
          })
          .catch(error => {
            notifications.notify({ message: 'Aconteceu algum erro', error: true })
            setLoadingSubmit(false)
          })
      }
    } else {
      notifications.notify({ message: 'Preencha o formulário corretamente', error: true })
    }
  }

  return (
    <ThemeProvider theme={Styles.themes.light}>
      <div style={{ ...Styles.center, ...Styles.full }}>
        <div style={{ ...Styles.fullAbsolute, ...Styles.background.light, opacity: 0.9 }} />
        <div style={{ ...Styles.full,/*  ...overlay,  */display: 'flex' }}>
          <div style={Styles.full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={1}
              style={Styles.full}
              freeMode
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <div style={{ display: 'flex' }}>
                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                      boxSizing: 'border-box',
                      width: '100%',
                    }}
                  >
                    <UI.Box.Column
                    >
                      <Material.Container>
                        <Material.Typography variant="h4" color="primary">
                          Agendamento
                        </Material.Typography>

                        <Material.Breadcrumbs
                          separator={<NavigateNextIcon fontSize="small" />}
                          aria-label="breadcrumb"
                        >
                          <Material.Link color="inherit" >
                            Orçamento
                          </Material.Link>
                          <Material.Link color="inherit" >
                            Formas de Pagamento
                          </Material.Link>
                          <Material.Link color="inherit" >
                            Detalhes do Frete
                          </Material.Link>
                          <Material.Link color="textPrimary" >
                            Agendamento
                          </Material.Link>
                        </Material.Breadcrumbs>
                        <div style={{ paddingBottom: '5vmin' }} />
                        <Material.Container>
                          <Material.Typography variant="h6" color="primary">
                            Agora vamos detalhar os dados pessoais para contato com a empresa! Preencha o formulário com as suas informações pessoais para que possamos entrar em contato
                          </Material.Typography>
                          <div style={{ paddingBottom: '2%' }} />
                          <UI.LoadingComponent
                            loading={props.loadingData}
                          />
                          {FormularyComponent}
                          <div style={{ paddingBottom: '2%' }} />
                          <Material.Button
                            variant="contained"
                            color="secondary"
                            style={{ width: '100%' }}
                            onClick={nextSectionSlide}
                          >
                            {
                              loadingSubmit
                                ? (
                                  <Material.CircularProgress />
                                )
                                : (
                                  <Material.Typography variant="h4">
                                    Finalizar Agendamento
                                  </Material.Typography>
                                )
                            }
                          </Material.Button>
                          <div style={{ paddingBottom: '2%' }} />
                        </Material.Container>
                      </Material.Container>
                    </UI.Box.Column>
                  </UI.Box.Column>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}