import React from 'react'

import styled from 'styled-components'

import { Swiper, SwiperSlide } from 'swiper/react';

import * as Material from '@material-ui/core';

import * as Styles from '../styles'

import * as UI from '../components'

const ImageSlider = styled(Swiper)`
  .swiper-scrollbar {
    background: gray;
  }
  .swiper-scrollbar-drag {
    background: white;
  }
  .swiper-pagination-bullet {
    background: gray;
    opacity: 1;
  }
  .swiper-pagination-bullet-active {
    background: white;
  }
`

/* const defaultEncartes = [
  {
    name: 'Cesta Modelo 1',
    details: [
      { name: 'Peso', value: '75 KG' },
      { name: 'Contém', value: '22 itens' },
      { name: 'Preço', value: 'R$ 79,00', highlight: true },
    ],
    images: products.products
  }, */

export default function Encarte(props = {}) {

  const {
    item,
    addItem,
    //refs,
    notifications,
    //sectionIndex,
  } = props

  const ref = React.useRef()
  const refMain = React.useRef()

  const update = React.useCallback(function update() {
    setTimeout(() => {
      ref.current.swiper.update()
      ref.current.swiper.scrollbar.updateSize()
      refMain.current.swiper.update()
    }, 200)
  }, [ref])

  React.useEffect(() => {
    setTimeout(update, 300)
  }, [item, ref, refMain, update])

  function addToCart(item) {
    return () => {
      if (addItem) addItem(item)
      notifications.notify({ message: 'Item adicionado ao carrinho!', success: true })
      //(refs?.site?.current?.swiper).slideNext();
      //console.log(refs?.[`section${sectionIndex}`]?.current?.swiper)
      //(refs?.[`section${sectionIndex + 1}`]?.current?.swiper).slideTo(0);
    }
  }

  return (
    <div style={{ ...Styles.center, ...Styles.full }}>
      <div style={{ ...Styles.full, ...Styles.overlay, display: 'flex'/* , flexDirection: 'column'  */ }}>
        <div style={Styles.full}>
          <Swiper
            scrollbar={{ draggable: true, hide: false }}
            slidesPerView='auto'
            direction="vertical"
            spaceBetween={0}
            threshold={1}
            style={Styles.full}
            freeMode
            nested
            ref={ref}
          >
            <SwiperSlide style={{ height: 'auto' }}>
              <div
                style={{
                  //display: 'flex',
                }}
              >
                <Material.Container>

                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                    }}
                  >
                    <div style={{
                      position: 'relative',
                      ...Styles.border.radius,
                      overflow: 'hidden',
                      //height: '800px'
                    }}>
                      <div style={{
                        ...Styles.background.primary,
                        ...Styles.fullAbsolute,
                        //...box.shadow,
                        //opacity: 0.85
                      }} />
                      <UI.Builder.Slides
                        SwiperComponent={ImageSlider}
                        useRef={refMain}
                        slides={item.images}
                        style={{
                          zIndex: 1,
                          height: 'auto',
                          //...box.shadowFilter
                        }}
                        speed={500}
                        autoplay={false}
                        imageStyle={{
                          objectFit: 'contain',
                          width: '100%',
                          height: '100%',
                          //width: '90%',
                          //height: '90%',
                          //margin: '5%',
                          //pointerEvents: 'all'
                        }}
                        nested
                        /* zoom={{
                          toggle: false,
                          maxRatio: 2
                        }} */
                        effect="slide"
                        scrollbar={{ draggable: true }}
                        direction="horizontal"
                        pagination
                        onLoad={update}
                      />
                      <div style={{

                        padding: '2rem',
                      }}>
                        <div style={{
                          ...Styles.heading,
                          ...Styles.foreground.light,
                          padding: 0
                        }}>{item.name}</div>
                        {
                          item.details.map((detail, index) => {
                            const { name, value, highlight } = detail
                            return (
                              <div
                                key={`${name}_${index}`}
                                style={{
                                  ...Styles.title,
                                  ...Styles.foreground.secondary,
                                  fontWeight: 'normal',
                                  display: 'flex',
                                  flexDirection: 'row',
                                  justifyContent: 'space-between'
                                }}>
                                <div style={Styles.foreground.secondary}>
                                  {name}
                                </div>
                                <div
                                  style={{
                                    ...Styles.foreground.light,
                                    //...text.shadow,
                                    ...(!highlight ? {} : { fontSize: '10vmin' }),
                                    textAlign: 'right',
                                  }}
                                >
                                  {value}
                                </div>
                              </div>
                            )
                          })
                        }
                        <div
                          style={{
                            cursor: 'pointer',
                            ...Styles.title,
                            ...Styles.background.secondary,
                            ...Styles.foreground.light,
                            ...Styles.text.shadow,
                            ...Styles.box.shadow,
                            ...Styles.border.radius,
                            textAlign: 'center'
                          }}
                          onClick={addToCart(item)}
                        >
                          EU QUERO
                        </div>
                      </div>
                    </div>
                  </UI.Box.Column>
                </Material.Container>
              </div>

            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div >
  )
}
