import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import * as router from "react-router-dom";

import * as Styles from '../styles'

import * as Assets from '../assets'

import Builder from './Builder';

export default function BuildSite(props) {

  const {
    structure,
    refs,
    getRef,
    path,
  } = props

  const ref = React.useRef()

  const history = router.useHistory()
  let location = router.useLocation()

  const [initialSlide] = React.useState(() => {
    const result = router.matchPath(history.location.pathname, {
      path: path,
      exact: true,
      strict: true,
    });
    return path.indexOf(result.path)
  })

  React.useEffect(() => {
    const result = router.matchPath(location.pathname, {
      path: path,
      exact: true,
      strict: true,
    });
    if (ref.current) ref.current.swiper.slideTo(path.indexOf(result.path))
  }, [location, path])

  if (getRef) getRef(ref)

  React.useEffect(() => {
    setTimeout(() => {
      ref.current.swiper.update()
    }, 200)
  }, [structure, ref])

  const onSlideChange = React.useCallback(function onSlideChange(swiper) {
    const newPath = path[swiper.activeIndex]
    /* const result = router.matchPath(newPath, {
      path: path,
      exact: false,
      strict: false,
    }); */
    history.push(newPath)
  }, [history, path])

  const Component = React.useMemo(() => (
    <div style={Styles.full}>
      <img
        alt={`background`}
        style={{ ...Styles.imageSlide, ...Styles.fullAbsolute }}
        src={Assets.backgrounds.bg1}
      />
      <Swiper
        ref={ref}
        initialSlide={initialSlide}
        spaceBetween={0}
        slidesPerView={1}
        threshold={20}
        //onSlideChange={onSlideChange}
        onSlideChangeTransitionEnd={onSlideChange}
        direction="vertical"
        //loop
        style={Styles.full}
      //scrollbar={{ draggable: true, hide: true }}
      >{
          structure.map((section, index) => (
            <SwiperSlide
              key={`section${index}`}
            >
              {
                (
                  typeof section === 'object'
                  && !Array.isArray(section)
                ) && (
                  <Builder.Section
                    index={index}
                    scenes={section.content}
                    refs={{ ...refs, site: ref }}
                    {...section.props}
                  />
                )
              }
              {
                (
                  typeof section === 'object'
                  && Array.isArray(section)
                ) && (
                  <Builder.Section
                    index={index}
                    scenes={section}
                    refs={{ ...refs, site: ref }}
                  />
                )
              }
            </SwiperSlide>
          ))
        }</Swiper>
    </div>
  ), [structure, onSlideChange, refs, initialSlide])

  return Component
}