import React from 'react'

import {
  overlay,
  full,
  center,
  heading
} from '../styles'

export default function BuildScene(props) {
  const { scene } = props
  switch (typeof scene) {
    case 'string':
      return (
        <div style={{ ...center, ...full }}>
          <div style={{ ...full, ...overlay }}>
            <div style={heading}>
              {scene}
            </div>
          </div>
        </div>
      )
    default:
      return (
        <div style={{ ...center, ...full }}>
          {React.cloneElement(scene, props)}
        </div>
      )
  }
}