import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import * as styles from '../styles'

export default function Slides(props) {
  const {
    SwiperComponent = Swiper,
    alt = 'background',
    zoom,
    imageStyle,
    useRef,
    slides,
    style,
    delay = 5000,
    slideProps,
    renderItem,
    onLoad,
    ...rest
  } = props

  //const [zoomed, setZoomed] = useState(false)

  React.useEffect(() => {
    useRef.current.swiper.allowTouchMove = slides.length > 1
  }, [useRef, slides])

  function toggle() {
    const { current = { swiper: { zoom: { out: console.error } } } } = useRef
    if (current.swiper.zoom.scale === 1/*  && !zoomed */) {
      //setZoomed(true)
      current.swiper.zoom.in()
      //current.swiper.autoplay.stop()
    } else if (current.swiper.zoom.scale > 1 /* && zoomed */) {
      //setZoomed(false)
      current.swiper.zoom.out()
      //current.swiper.autoplay.start()
    }

  }

  const zoomProps = {
    className: "swiper-zoom-container",
    onDoubleClick: toggle
  }

  return (
    <SwiperComponent
      ref={useRef}
      threshold={10}
      spaceBetween={0}
      slidesPerView={1}
      direction="horizontal"
      effect="fade"
      //navigation
      //pagination
      //loop
      nested
      speed={2500}
      autoplay={{ delay }}
      zoom={zoom}
      allowTouchMove={slides.length > 1}
      style={{
        ...styles.full,
        //s...fullAbsolute,
        ...(
          zoom ? { overflow: 'visible' } : {}
        ),
        ...style,
      }}
      {...rest}
    >
      {
        slides.map((slide, index) => {
          const content = (
            <img
              src={slide}
              onLoad={onLoad}
              alt={`${alt}_slide_${index}`}
              style={{ ...styles.imageSlide, ...imageStyle }}
            />
          )
          return (
            <SwiperSlide key={`${alt}_slide_${index}`} {...slideProps}>
              <div {...(!zoom ? { style: styles.full } : zoomProps)}>
                {!renderItem ? content : renderItem({ content }, index)}
              </div>
            </SwiperSlide>
          )
        })
      }
    </SwiperComponent>
  )
}
