import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { ThemeProvider } from '@material-ui/core/styles';

import {
  full,
  center,
  background,
  fullAbsolute,
  themes,
} from '../styles'

import * as UI from '../components'

import {
  Button,
  Typography,
  Container,
  CircularProgress,
} from '@material-ui/core';

export default function Contato(props = {}) {

  const {
    dimensions,
    siteData,
    api,
    notifications,
  } = props

  const ref = React.useRef()

  const [loadingSubmit, setLoadingSubmit] = React.useState(false)

  const [data, setData] = React.useState({})

  React.useEffect(() => {
    const rawData = siteData['*Contato']
    setData(rawData && rawData.length ? rawData[0] : {})
  }, [siteData])

  const {
    Component: FormularyComponent,
    values,
    form,
  } = UI.Formulary({
    data,
    name: 'Contato'
  })

  const update = React.useCallback(() => {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  React.useEffect(() => {
    setTimeout(update, 200)
  }, [form, update])

  React.useEffect(update, [dimensions])

  function validateForm() {
    const results = form.map(field => {
      if (field.required && !values[field.name]) return false
      return true
    })
    return results.filter(result => !!result).length === results.length
  }

  function onClickSubmit() {
    setLoadingSubmit(true)
    if (!validateForm()) {
      setTimeout(() => {
        notifications.notify({ message: 'Preencha o formulário corretamente!', error: true })
        setLoadingSubmit(false)
      }, 1500)
    } else {
      api.get('/make-contact/1f292dcf.668556', { params: values })
        .then(response => {
          notifications.notify({ message: 'Contato enviado com sucesso!', success: true })
          setLoadingSubmit(false)
        })
        .catch(error => {
          notifications.notify({ message: 'Aconteceu algum erro', error: true })
          setLoadingSubmit(false)
        })
    }
  }

  return (
    <ThemeProvider theme={themes.light}>
      <div style={{ ...center, ...full }}>
        <div style={{ ...fullAbsolute, ...background.light, opacity: 0.9 }} />
        <div style={{ ...full,/*  ...overlay,  */display: 'flex' }}>
          <div style={full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={1}
              style={full}
              freeMode
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <div style={{ display: 'flex' }}>
                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                      boxSizing: 'border-box',
                      width: '100%',
                    }}
                  >
                    <UI.Box.Column
                    >
                      <Container>
                        <Typography variant="h4" color="primary">
                          Contato
                        </Typography>

                        <div style={{ paddingBottom: '5vmin' }} />
                        <Container>
                          <Typography variant="h6" color="primary">
                            Entre em contato com a gente! Preencha o formulário abaixo com seus dados e entraremos em contato em breve
                          </Typography>
                          <div style={{ paddingBottom: '2%' }} />
                          {FormularyComponent}
                          <UI.LoadingComponent loading={props.loadingData} />
                          <div style={{ paddingBottom: '2%' }} />
                          <Button
                            variant="contained"
                            color="secondary"
                            style={{ width: '100%' }}
                            onClick={onClickSubmit}
                          >
                            {
                              loadingSubmit
                                ? <CircularProgress />
                                : (
                                  <Typography variant="h4">
                                    Entrar em contato
                                  </Typography>
                                )
                            }
                          </Button>
                          <div style={{ paddingBottom: '2%' }} />
                        </Container>
                      </Container>
                    </UI.Box.Column>
                  </UI.Box.Column>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}