import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  Button,
  Typography,
  Link,
  //Paper,
  Container,
  Breadcrumbs,
} from '@material-ui/core';

import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import { ThemeProvider } from '@material-ui/core/styles';

import {
  full,
  center,
  background,
  fullAbsolute,
  themes,
} from '../styles'

import * as UI from '../components'

export default function Frete(props = {}) {

  const {
    dimensions,
    refs,
    siteData,
    notifications,
  } = props

  const ref = React.useRef()

  const [data, setData] = React.useState({})

  React.useEffect(() => {
    const rawData = siteData['*Frete']
    setData(rawData && rawData.length ? rawData[0] : {})
  }, [siteData])

  const {
    Component: FormularyComponent,
    values,
    form,
  } = UI.Formulary({
    data
  })

  const update = React.useCallback(() => {
    ref.current.swiper.update()
    ref.current.swiper.scrollbar.updateSize()
  }, [ref])

  React.useEffect(() => {
    setTimeout(update, 200)
  }, [form, update, dimensions])

  React.useEffect(() => {
    const { swiper } = ref.current
    const { scrollbar } = swiper
    const { dragEl } = scrollbar
    dragEl.style.background = 'white'
  }, [ref])

  /* const details = [
    {
      name: 'prazo',
      label: 'Estimativa de Prazo',
      value: '01/10/2020'
    },
    {
      name: 'valor',
      label: 'Valor da Entrega',
      value: 'R$ 77,40',
      big: true,
    },
  ] */

  function validateForm() {
    const results = form.map(field => {
      if (field.required && !values[field.name]) return false
      return true
    })
    return results.filter(result => !!result).length === results.length
  }

  function nextSectionSlide() {
    if (validateForm()) {
      if (props.onChangeForm) props.onChangeForm({ Frete: values });
      (refs?.section?.current?.swiper).slideNext()
    } else {
      notifications.notify({ message: 'Preencha o formulário corretamente', error: true })
    }
  }

  return (
    <ThemeProvider theme={themes.light}>
      <div style={{ ...center, ...full }}>
        <div style={{ ...fullAbsolute, ...background.secondary, opacity: 0.9 }} />
        <div style={{ ...full,/*  ...overlay,  */display: 'flex' }}>
          <div style={full}>
            <Swiper
              scrollbar={{ draggable: true, hide: false }}
              slidesPerView='auto'
              direction="vertical"
              spaceBetween={0}
              threshold={1}
              style={full}
              freeMode
              ref={ref}
            >
              <SwiperSlide style={{ height: 'auto' }}>
                <div style={{ display: 'flex' }}>
                  <UI.Box.Column
                    style={{
                      padding: '5vmin',
                      boxSizing: 'border-box',
                      width: '100%',
                    }}
                  >
                    <UI.Box.Column
                    >
                      <Container>
                        <Typography variant="h4" color="primary">
                          Detalhes do Frete
                        </Typography>

                        <Breadcrumbs
                          separator={<NavigateNextIcon fontSize="small" />}
                          aria-label="breadcrumb"
                        >
                          <Link color="inherit" >
                            Orçamento
                          </Link>
                          <Link color="inherit" >
                            Formas de Pagamento
                          </Link>
                          <Link color="textPrimary" >
                            Detalhes do Frete
                          </Link>
                          <Link color="inherit" >
                            Agendamento
                          </Link>
                        </Breadcrumbs>
                        <div style={{ paddingBottom: '5vmin' }} />
                        <Container>
                          <Typography variant="h6" color="primary">
                            Agora vamos preencher os detalhes do endereço para o frete! Preencha o formulário com as informações do seu endereço em detalhes
                          </Typography>
                          <div style={{ paddingBottom: '2%' }} />
                          <UI.LoadingComponent
                            loading={props.loadingData}
                            styleProgress={{
                              color: 'white'
                            }}
                          />
                          {FormularyComponent}
                          <div style={{ paddingBottom: '2%' }} />
                          {/* <Paper style={{ padding: '1rem' }} variant="elevation" elevation={10}>
                            <Typography variant="h5">
                              Detalhes do Frete
                            </Typography>
                            <div style={{ paddingBottom: '2%' }} />
                            {
                              details.map(detail => (
                                <UI.Box.Row key={`detail${detail.name}`} style={{ justifyContent: 'space-between', flexWrap: 'wrap' }}>
                                  <Typography
                                    variant={detail.big ? "h4" : "h5"}
                                  >
                                    {detail.label}
                                  </Typography>
                                  <Typography
                                    color="primary"
                                    variant={detail.big ? "h3" : "h4"}
                                  >
                                    {detail.value}
                                  </Typography>
                                </UI.Box.Row>
                              ))
                            }
                            <div style={{ paddingBottom: '2%' }} />
                            <Button
                              variant="contained"
                              color="primary"
                              style={{ width: '100%' }}
                              onClick={nextSectionSlide}
                            >
                              <Typography variant="h4">
                                Confirmar
                              </Typography>
                            </Button>
                          </Paper> */}

                          <Button
                            variant="contained"
                            color="primary"
                            style={{ width: '100%' }}
                            onClick={nextSectionSlide}
                          >
                            <Typography variant="h4">
                              Confirmar
                              </Typography>
                          </Button>
                        </Container>
                      </Container>
                    </UI.Box.Column>
                  </UI.Box.Column>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>
      </div >
    </ThemeProvider>
  )
}